.slide {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 10px;
    background-color: #55b9c2;
    transition: .4s;
    padding: 8px;
    color: #fff;
}

.slide:before {
    position: absolute;
    content: "";
    height: 25px;
    width: 25px;
    left: 20px;
    bottom: 1px;
    background-color: white;
    transition: .4s;
}

input:checked + .slide {
    background-color: #8CE196;
    padding-left: 35px;
}

input:focus + .slide {
    box-shadow: 0 0 1px #55b9c2;
}

input:checked + .slide:before {
    transform: translateX(26px);
    left: -25px;
}

.slide.round {
    border-radius: 40px;
}

.slide.round:before {
    border-radius: 50%;
}

.btn-update.add-office {
    margin-top: 6px;
    margin-bottom: 35px;
    padding: 9px 5px;
}

.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
}

.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: #fff transparent #fff transparent;
    -webkit-animation: lds-dual-ring 1.2s linear infinite;
            animation: lds-dual-ring 1.2s linear infinite;
}

@-webkit-keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
